import Grid from "@mui/material/Grid";

// Material Dashboard 2 React components
import MDBox from "components/MDBox";

// Material Dashboard 2 React example components
import DashboardLayout from "examples/LayoutContainers/DashboardLayout";
import DashboardNavbar from "examples/Navbars/DashboardNavbar";
// import Footer from "examples/Footer";
// import ReportsBarChart from "examples/Charts/BarCharts/ReportsBarChart";
// import ReportsLineChart from "examples/Charts/LineCharts/ReportsLineChart";
import ComplexStatisticsCard from "examples/Cards/StatisticsCards/ComplexStatisticsCard";

// Data
// import reportsBarChartData from "layouts/dashboard/data/reportsBarChartData";
// import reportsLineChartData from "layouts/dashboard/data/reportsLineChartData";

// Dashboard components
// import Projects from "layouts/dashboard/components/Projects";
// import OrdersOverview from "layouts/dashboard/components/OrdersOverview";
import reduxContainer from "redux/containers/containers";
// import MITable from "examples/miTable";
import { useNavigate } from "react-router-dom";
import MDTypography from "components/MDTypography";
import { Action } from 'layouts/ProjectsTable/data/authorsTableData'
import MDBadge from "components/MDBadge";
import ProjectDataTable from "examples/projectsTable";
import { Card } from "@mui/material";

function Dashboard({ reduxActions, reduxState }) {
  const project_list = reduxState.project_list?.CustomerProjects
  const user = reduxState?.userDetails

  const projectQueue = project_list?.filter( item => {
    return item.status === 'Approval'
  })
  
  const activeProject = project_list?.filter( item => {
    return item.status === 'Ongoing'
  })

  const navigate = useNavigate()

  const handleOpen = () => {
    reduxActions.setAlert(true)
  }

  const getUserRole = () => {
    let val = reduxState.userDetails?.roles
    if (val.includes("Graphic-Designer")) {
      return "(GRAPHIC DESIGNER)"
    } else if (val.includes("Project-Manager")) {
      return "(PROJECT MANAGER)"
    } else if (val.includes("Admin")) {
      return "(ADMIN)"
    } else {
      return "(CUSTOMER)"
    }
  }

  function projectActiveorNot(id) {
    // console.log('ID =>', id)
    reduxActions.getID(id)
    let projectID = project_list[id].hasOwnProperty("_id") ? project_list[id]?._id : project_list[id]?.id
    if (user?.roles?.includes("Project-Manager") || user?.roles?.includes("Graphic-Designer") || user?.roles?.includes("Admin")) {
      navigate("/chat/" + projectID)
      return
    } else {
      project_list[id]?.is_active ? navigate("/chat/" + projectID) : handleOpen()
    }
  }

  function projectStatus(status) {
    switch (status) {
      case 'Approval':
        return "Approval"
      case 'Completed':
        return "Completed"
      case 'Ongoing':
        return "Ongoing"
      case 'HeadsUp':
        return "HeadsUp"
      default:
        return "End"
    }
  }
  function getStatusStyle(status) {
    switch (status) {
      case 'Approval':
        return "#c5495d"
      case 'Completed':
        return "#FFE135"
      case 'Ongoing':
        return "#FFE135"
      case 'HeadsUp':
        return "#FFE135"
      default:
        return "#c5495d"
    }
  }
  const rows = project_list?.length ? project_list.map((item, i) => {

    const date = new Date(item.createdAt);
    const year = date.getFullYear();
    const month = String(date.getMonth() + 1).padStart(2, "0"); // Adding 1 because months are zero-indexed
    const day = String(date.getDate()).padStart(2, "0");
    let hours = date.getHours();
    const minutes = String(date.getMinutes()).padStart(2, "0");
    const seconds = String(date.getSeconds()).padStart(2, "0");
    let ampm = "AM";

    // Convert to 12-hour format and set AM/PM
    if (hours >= 12) {
      ampm = "PM";
      if (hours > 12) {
        hours -= 12;
      }
    }
    hours = String(hours).padStart(2, "0");
    const readableTimestamp = `${year}-${month}-${day} ${hours}:${minutes}:${seconds} ${ampm}`;

    console.log('=>', item)
    // onClick={() => isfunc === true ? projectActiveorNot(row?.id) : null}
    return {
      name: (
        <MDBox lineHeight={1}>
          <MDTypography display="block" sx={{ textDecoration: 'underline ' + '!important' }} variant="button" fontWeight="medium">
            <MDBox sx={{ "&:hover": { color: "blue" } }} onClick={() => projectActiveorNot(project_list?.indexOf(item))}>
              {item?.name}
            </MDBox>
          </MDTypography>
        </MDBox>),

      team_members: <MDTypography display="flex" flexDirection="column" gap="10px">
        {item.team_members?.length > 0 ? item.team_members.map(item => <MDTypography color="#333" variant="h6">{item.name}</MDTypography>) :
          <MDTypography color="#333" fontSize="small" variant="h6">Currently not Assigned to <br /> any Team members</MDTypography>}
      </MDTypography>,

      status: <MDBox ml={-1}>
        <MDBadge badgeContent={projectStatus(item?.status)}
          sx={{
            "& .MuiBadge-badge":
              { background: getStatusStyle(item?.status), color: 'dark', textTransform: 'capitalize', fontSize: ".8rem" }
          }} circular="true" size="lg" />
      </MDBox>,
      project_category: <MDTypography variant="h6" color="text" fontWeight="medium">
        {item.project_category}
      </MDTypography>,
      active: <MDTypography color="dark" variant="p">{!item.is_active ? "Not Active" : item.updatedAt}</MDTypography>,
      createdAt: <MDTypography color="dark" variant="p">{readableTimestamp}</MDTypography>,
      action: <MDTypography component="a" href="#" variant="caption" color="text" fontWeight="medium">
        <Action />
      </MDTypography>

    }
  }) : []

  const columns = [
    { Header: "author", accessor: "name", align: "left", },
    { Header: "Team Member", accessor: "team_members", align: "left" },
    { Header: "status", accessor: "status", align: "center" },
    { Header: "Category", accessor: "project_category", align: "center" },
    { Header: "Active on", accessor: "active", align: "center" },
    { Header: "Submitted on", accessor: "createdAt", align: "center" },
    { Header: "Action", accessor: "action", align: "center" },
  ]

  return (
    <DashboardLayout>
      <DashboardNavbar />
      <MDBox py={3}>
        <Grid container spacing={3}>
          <Grid item xs={12} md={6} lg={4}>
            <MDBox mb={1.5}>
              <ComplexStatisticsCard
                color="dark"
                icon="weekend"
                title="Good Evening"
                message={reduxState.userDetails?.name ? `Hi, ${reduxState.userDetails?.name.toUpperCase()} ${getUserRole()}` : ""}
              // count={281}
              // percentage={{
              //   color: "success",
              //   amount: "+55%",
              //   label: "than lask week",
              // }}
              />
            </MDBox>
          </Grid>
          <Grid item xs={12} md={6} lg={4}>
            <MDBox mb={1.5}>
              <ComplexStatisticsCard
                icon="leaderboard"
                title="Ready for review"
                message={activeProject?.length ? activeProject?.length + " Active Project" : "0 Active Project "}
                // count="2,300"
              // percentage={{
              //   color: "success",
              //   amount: "+3%",
              //   label: "than last month",
              // }}
              />
            </MDBox>
          </Grid>
          <Grid item xs={12} md={6} lg={4}>
            <MDBox mb={1.5}>
              <ComplexStatisticsCard
                color="success"
                icon="store"
                title="In Queue"
                message={`${projectQueue?.length ? projectQueue?.length + " Project in Queue" : "0 Projects in Queue" }`}
              // percentage={{
              //   color: "success",
              //   amount: "+1%",
              //   label: "than yesterday",
              // }}
              />
            </MDBox>
          </Grid>
          <Grid item xxl={12} spacing={2} display={"flex"} justifyContent={"center"} alignItems={"center"}>
            <Card>
              <MDBox
                // mx={2}
                mt={3}
                sx={({ palette: { light, grey } }) => ({
                  // backgroundColor : light.cream,
                })}>
                <MDBox pt={3}>
                  <ProjectDataTable
                    table={{ columns, rows }}
                    isSorted={false}
                    entriesPerPage={false}
                    showTotalEntries={false}
                    isfunc={true}
                    noEndBorder
                  />
                  {!rows.length ? <MDTypography textAlign="center" p={1} component="h4">No Projects Found</MDTypography> : null}
                </MDBox>

              </MDBox>
            </Card>
          </Grid>
        </Grid>
      </MDBox>
      {/* <Footer /> */}
    </DashboardLayout>
  );
}

export default reduxContainer(Dashboard);
