import { USER_DETAILS, SHOW_MODAL, PROJECT_CATEGORY, CUSTOMER_BRAND, NEW_BRAND, STATUS, IS_ALERT, CUSTOMER_PROJECTS, USER_AVATAR_URL, PROJECT_ID } from "redux/actions/actions"

const initialState = {
    userDetails: {},
    isModalShow: false,
    statuses: [
        "All",
        "Archived",
        "Cancelled",
        "Approval",
        "Completed",
        "Ongoing",
        "For Review",
        "Draft",
        "Heads Up!",
    ],
    category: [
        "Graphic Designs",
        "Copywriting",
        "Illustrations",
        "Video Editing",
        "Motion Graphics",
        "Web Development",
        "Voice Overs",
        "Social Media Management Lite",
    ],
    brand: [
        "Nike",
        "Puma",
        "Ides",
        "Chen one"
    ],
    project_cat: "",
    project_list: [],
    user_avatar : "",
    project_ID : "",
    isStatus : "",
    isAlert : false,
    new_brand : false,
    customerBrand : []
}
const UserReducers = (state = initialState, action) => {
    const { type, payload } = action
    switch (type) {
        case USER_DETAILS:
            return {
                ...state,
                userDetails: payload
            }
        case SHOW_MODAL: {
            return {
                ...state,
                isModalShow: payload
            }
        }
        case PROJECT_CATEGORY: {
            return {
                ...state,
                project_cat: payload
            }
        }
        case CUSTOMER_PROJECTS : {
            return {
                ...state,
                project_list : payload
            }
        }
        case USER_AVATAR_URL : {
            return {
                ...state,
                user_avatar : payload
            }
        }
        case PROJECT_ID : {
            return {
                ...state,
                project_ID : payload
            }
        }
        case IS_ALERT : {
            return {
                ...state,
                isAlert : payload
            }
        }
        case STATUS : {
            return {
                ...state,
                isStatus : payload
            }
        }
        case NEW_BRAND : {
            return {
                ...state,
                new_brand : payload
            }
        }
        case CUSTOMER_BRAND : {
            return {
                ...state,
                customerBrand : payload
            }
        }

        default:
            return state
    }
}
export default UserReducers
