import React, { useEffect, useRef, useState } from 'react'
import DashboardLayout from 'examples/LayoutContainers/DashboardLayout'
import DashboardNavbar from 'examples/Navbars/DashboardNavbar'
import Grid from '@mui/material/Grid'
import MDBox from 'components/MDBox'
import MDInput from 'components/MDInput'
import MDButton from 'components/MDButton'
import { SendOutlined } from '@mui/icons-material'
import MDTypography from 'components/MDTypography'
import { useParams } from 'react-router-dom'
import apiClient from 'api/apiClient'
import reduxContainer from 'redux/containers/containers'
import { useCallback } from 'react'
// import { io } from "socket.io-client"
import { useSelector } from 'react-redux'
import ImageAvatar from 'assets/mi-banana-icons/default-profile.png'
import TeamMembers from './team-members/TeamMembers'
import SelectMembers from './team-members/select-members'
import FormControlLabel from '@mui/material/FormControlLabel'
import Checkbox from '@mui/material/Checkbox'
import "./style.css"
import { showFilesModal } from 'redux/global/global-functions'
import Modal from 'examples/ModalLayout'
import MDSnackbar from 'components/MDSnackbar'

// const socket = io.connect('https://test-vercel-amber-theta.vercel.app', {
//     withCredentials: true
// })
// const socket = io.connect("http://localhost:4006", {
//     withCredentials: true
// })

// socket.on('connect', () => {
//     console.log("Connected to Socket Server ")


// })

const ITEM_HEIGHT = 48;
const ITEM_PADDING_TOP = 8;
const MenuProps = {
    PaperProps: {
        style: {
            maxHeight: ITEM_HEIGHT * 4.5 + ITEM_PADDING_TOP,
        },
    },
};
const Chating = ({ reduxState }) => {
    const c = console.log.bind(this)
    const currentTime = new Date(); // Get the current date and time
    const formattedTime = currentTime.toLocaleTimeString();
    const [msgArray, setMsgArray] = useState([])
    const [limit, setLimit] = useState(15)
    const [message, sendMessage] = useState("")
    const chatContainerRef = useRef(null);
    const [modalState, setModalState] = useState(false)
    const [hideChatBox, setHideChatBox] = useState(false)
    const [memberName, setMemberName] = useState({})
    const [IsComingMsg, setIsComingMsg] = useState(false)


    const [respMessage, setRespMessage] = useState("")
    const [successSB, setSuccessSB] = useState(false);

    const openSuccessSB = () => setSuccessSB(true);
    const closeSuccessSB = () => setSuccessSB(false);

    const { id } = useParams()
    const { id: user, name } = reduxState?.userDetails
    // const socketRef = useRef(socket)
    let avatar = ''
    let check = useSelector(state => state.user_avatar)

    const personProject = reduxState.project_list?.CustomerProjects?.filter(item => {
        return item._id === id
    })
    const team_members = personProject[0]?.team_members
    const isManager = reduxState.userDetails?.roles?.includes("Project-Manager") ? true : false
    const [isActive, setIsActive] = useState(personProject[0]?.is_active)

    c('team-mem ', team_members)

    if (check) {
        avatar = check
    } else {
        avatar = ImageAvatar
    }

    const handleChange = (event) => {
        // console.log(event.target)
        setMemberName(event.target.value)
    }

    const handleCheckbox = useCallback(() => setIsActive(prev => !prev), [isActive])

    const handleKeyPress = (event) => {
        if (event.key === "Enter") {
        }
    }
    const onSendMessage = useCallback(async (event) => {
        event.preventDefault();
        if (message === '') {
            return
        }
        const data = {
            project_id: id,
            chat_message: {
                user: user,
                name: name,
                avatar: avatar,
                time_data: formattedTime,
                message: message
            }
        }

        setMsgArray(prev => prev ? [...prev, data.chat_message] : [data.chat_message])
        // socketRef.current.emit("room-message", data.chat_message, id)
        await apiClient.put("/chat-message", data)
            .then(({ data }) => {
                setIsComingMsg(true)
                console.log("Message ", data?.message)
            })
            .catch(e => console.log("Chat Send Error ", e?.response))
        sendMessage("")
    }, [message])

    // function joinChatRoom() {
    //     setHideChatBox(true)
    //     socketRef.current.emit("room-message", '', id)
    // }

    const getChatMessage = async () => {
        await apiClient.get('/chat-message/' + id)
            .then(({ data }) => {
                if (data.chat?.chat_msg?.length) {
                    setMsgArray(data.chat?.chat_msg)
                }
            })
            .catch(e => console.error('error ', e.response))

    }


    const SubmitProject = () => {
        if (isManager) {
            let project = personProject[0]
            project.team_members = [memberName]
            project.status = "Ongoing"
            project.is_active = isActive

            let data = {
                project_id: personProject[0]?._id,
                project_data: project
            }
            apiClient.patch("/graphic-project", data)
                .then(({ data }) => {
                    console.log(data)
                    setRespMessage(data?.message)
                    setTimeout(() => {
                        openSuccessSB()
                    }, 600)
                })
                .catch(e => {
                    console.error('Error assgin project => ', e?.response?.data?.message)
                })
        }

    }

    // useEffect(() => {
    //     socketRef.current.emit("project_id", id, limit)
    //     socketRef.current.on("found", (result) => {
    //         // console.log('result => ', result)
    //         setMsgArray(result)
    //     })
    //     socketRef.current.on("message", (message) => {
    //         // console.log('message ', message)
    //         setMsgArray(prev => [...prev, message])
    //     })
    // }, [socketRef.current])

    useEffect(() => {
        getChatMessage()
    }, [IsComingMsg])


    useEffect(() => {
        if (chatContainerRef.current) {
            chatContainerRef.current.scrollTop = chatContainerRef.current.scrollHeight;
        }
    }, [msgArray])

    const renderSuccessSB = (
        <MDSnackbar
            color="success"
            icon="check"
            title="SUCCESS"
            content={respMessage}
            dateTime={new Date().toLocaleTimeString('pk')}
            open={successSB}
            onClose={closeSuccessSB}
            close={closeSuccessSB}
            bgWhite
        />
    );


    return (
        <DashboardLayout>
            <DashboardNavbar />
            <Modal
                title="Files"
                open={modalState}
                setOpen={setModalState}
            >
                <h1>Files Show here</h1>+
            </Modal>
            <MDBox width="100%">
                <Grid container spacing={2}>
                    <Grid item xxl={7} xl={7} lg={7} md={12} xs={12}>
                        <MDBox {...styleProps} sx={{ overflowY: "scroll" }}>
                            <MDTypography sx={({ palette: { primary } }) => ({
                                backgroundColor: primary.main,
                                padding: '10px',
                                borderTopLeftRadius: '4px',
                                borderTopRightRadius: '6px',
                            })}
                                variant="h4" pb={1}>{personProject[0]?.project_title}</MDTypography>
                            <Grid container justifyContent={"space-between"} spacing={4} padding={2} alignItems={"center"}>
                                <Grid item xxl={6}>
                                    <MDTypography variant="h4">Team Members</MDTypography>
                                    {!team_members?.length ?
                                        <MDTypography variant="p" pl={1} pb={2} fontSize="small">
                                            Not Assigned to any Team members
                                        </MDTypography> : null}
                                    <br />
                                    {
                                        team_members?.length ?
                                            team_members?.map((item, i) => <TeamMembers key={i} team_member={item} />
                                            ) : null
                                    }
                                    {isManager ?
                                        <SelectMembers
                                            manager={isManager}
                                            memberName={memberName}
                                            handleChange={handleChange}
                                        />
                                        : null
                                    }
                                </Grid>
                                <Grid item xxl={6} alignSelf={"baseline"} >
                                    <MDTypography variant="h4">Author</MDTypography>
                                    <MDTypography variant="h6">{personProject[0]?.name}</MDTypography>
                                </Grid>
                                <Grid item xxl={4}>
                                    <MDTypography variant="h4">Project Category</MDTypography>
                                    <MDTypography variant="h6">{personProject[0]?.project_category}</MDTypography>
                                </Grid>
                                <Grid item xxl={4}>
                                    <MDTypography variant="h4">Design Type</MDTypography>
                                    <MDTypography variant="h6">{personProject[0]?.design_type}</MDTypography>
                                </Grid>
                                <Grid item xxl={4}>
                                    <MDTypography variant="h4">Brand</MDTypography>
                                    <MDTypography variant="h6">{personProject[0]?.brand}</MDTypography>
                                </Grid>
                                <Grid item xxl={4}>
                                    <MDTypography variant="h4">Resources</MDTypography>
                                    <MDTypography variant="h6">Project content</MDTypography>
                                </Grid>
                                <Grid item xxl={4}>
                                    <MDTypography variant="h4">Project Description</MDTypography>
                                    <MDTypography variant="h6">{personProject[0]?.project_description}</MDTypography>
                                </Grid>
                                <Grid item xxl={4}>
                                    <MDTypography variant="h4">Audience</MDTypography>
                                    <MDTypography variant="h6">{personProject[0]?.describe_audience}</MDTypography>
                                </Grid>
                                <Grid item xxl={4}>
                                    <MDTypography variant="h4">Sizes</MDTypography>
                                    <MDTypography variant="h6">{personProject[0]?.sizes}</MDTypography>
                                </Grid>
                                <Grid item xxl={4}>
                                    <MDTypography variant="h4">Files</MDTypography>
                                    <MDTypography variant="a" fontSize="small"
                                        onClick={() => showFilesModal(modalState, setModalState)}
                                        sx={{
                                            textDecoration: "underline",
                                            cursor: 'pointer',
                                            color: 'blue'
                                        }}>Show project files</MDTypography>
                                </Grid>
                                <Grid item xxl={4}>
                                    <MDTypography variant="h4">Specific Software Name</MDTypography>
                                    <MDTypography variant="h6">{personProject[0]?.specific_software_names}</MDTypography>
                                </Grid>
                                <Grid item xxl={4}>
                                    <MDTypography variant="h4">is Active</MDTypography>
                                    <MDTypography variant="h6" fontSize="small" >{!personProject[0]?.is_active ? "This Project is not active" : "Project is Active"}</MDTypography>
                                    <FormControlLabel required control={<Checkbox checked={isActive}
                                        onChange={handleCheckbox}
                                        sx={({ palette: { warning } }) => ({
                                            color: warning.dark,
                                            '&.Mui-checked': {
                                                color: warning.light,
                                            }
                                        })}
                                    />} label={`${!personProject[0]?.is_active ? 'Make Project Active' : 'Active'}`} />

                                </Grid>
                                {isManager ?
                                    <Grid item xxl={12} alignSelf={"flex-end"} textAlign={"right"}>
                                        <MDButton color="dark" bgColor="success" type="button" onClick={SubmitProject}>
                                            Submit
                                        </MDButton>
                                    </Grid>
                                    : null}
                            </Grid>

                        </MDBox>
                    </Grid>
                    <Grid item xxl={5} xl={5} lg={5} md={12} xs={12} position={"relative"}>
                        {/* {!hideChatBox ? <MDBox
                            position="absolute"
                            bgColor="light"
                            width="97.5%"
                            borderRadius="10px"
                            zIndex="99"
                            height="86%"
                            marginTop="17px"
                        // top="46%" 
                        // left="46%" 
                        >
                            <MDButton color="dark" sx={{ marginTop: "51%", marginLeft: "46%" }} onClick={joinChatRoom}>
                                Join Chat
                            </MDButton>
                        </MDBox> : null} */}
                        <MDBox {...styleProps}>
                            <Grid
                                container component={"form"}
                                onSubmit={onSendMessage}
                                justifyContent={"space-between"}
                                alignItems={"flex-start"}
                                flexDirection={"column"} >

                                <Grid item xxl={12} xl={12} lg={12} width={"100%"} sx={{ overflowY: "auto" }}>
                                    <MDBox {...stylesProps2.sx2} sx={stylesProps2.sx} p={2} ref={chatContainerRef}>

                                        <div className="chat" stlye={{ marginBlock: '10px' }}>
                                            {msgArray?.length ? msgArray.map((item, index, messages) => (
                                                <div
                                                    key={index}
                                                    className={`message ${item.user === reduxState.userDetails?.id ? 'right' : 'left'}`}
                                                    style={{ position: 'relative' }}
                                                >
                                                    {/* Display user name for the first message in a sequence */}
                                                    {/* {index === 0 || item.user !== messages[index - 1].user ? ( */}
                                                    <div className="user-name">

                                                        <img src={item.avatar} width={40} height={40}
                                                            loading='lazy'
                                                            style={{
                                                                borderRadius: 30,
                                                                // marginTop: '-8px',
                                                                display: 'inline-block',
                                                                width: '50px',
                                                                height: '50px',
                                                                // position: 'absolute',
                                                                top: '-32px',
                                                                left: item.user === user ? '70px' : '-9px'
                                                            }} />
                                                        <p>{item.name}</p>
                                                    </div>
                                                    <div className="message-content">{item.message}</div>
                                                    <span style={{ fontSize: '13px', fontWeight: '400', color: '#444' }}>{item.time_data ? item.time_data : null}</span>
                                                </div>
                                            )) : null}
                                        </div>
                                    </MDBox>

                                </Grid>
                                <Grid item xxl={12} xl={12} lg={12} width={"100%"}>
                                    <MDBox height={"8vh"} width="100%" p={2}
                                        sx={{
                                            borderBottomLeftRadius: "12px",
                                            borderBottomRightRadius: "12px",
                                            // backgroundColor : "#f1f2f2"
                                        }}
                                    >
                                        <MDInput
                                            placeholder="type your message here"
                                            value={message}
                                            onKeyPress={handleKeyPress}
                                            onChange={(e) => sendMessage(e.target.value)}
                                            sx={{
                                                width: "60%",
                                                color: "#444",
                                                fontFamily: "arial",
                                                fontSize: "1.2rem !important",
                                            }}

                                        />
                                        &nbsp;
                                        <MDButton
                                            type="button"
                                            sx={sendButton}
                                            size="small"
                                            color="dark"
                                            fontSize="small"
                                            onClick={getChatMessage}
                                            >
                                            Refresh Chat
                                        </MDButton >
                                        &nbsp;
                                        <MDButton
                                            endIcon={<SendOutlined fontSize='medium' />}
                                            type="submit"
                                            sx={sendButton}
                                            color="dark"
                                        >
                                            Send
                                        </MDButton >
                                    </MDBox>
                                </Grid>
                            </Grid>
                        </MDBox>
                    </Grid>
                    {renderSuccessSB}
                </Grid >
            </MDBox >
        </DashboardLayout >
    )
}


const Styles = {
    fontWeight: "bold",
    fontSize: "15px",
    marginLeft: 4
}

const stylesProps2 = {
    sx: {
        borderTopLeftRadius: "12px",
        borderTopRightRadius: "12px",
        overflowY: "auto",
    },
    sx2: {
        height: "72vh",
        width: "100%",
        bgColor: "#f0f0f0",
        // background-color: #f0f0f0;
    }
}

const sendButton = {
    borderRadius: "4px !important",
    minHeight: '42px',
    border: '1px solid #444',
}

const inComingMsg = {
    padding: '15px',
    variant: "h5",
    color: "light",
    mt: 1,
}
const inComingMsg2 = {
    // backgroundColor: "green",
    borderBottomLeftRadius: '18px',
    borderTopRightRadius: '18px',
    display: "inline-block"
}


const styleProps = {
    mt: 2,
    border: '5px solid #33333321',
    borderRadius: "10px",
    // bgColor: "primary",
    height: "80vh",
    overflowy: "auto"
}


export default reduxContainer(Chating)
