import React, { useEffect, useState } from 'react'
import Select from '@mui/material/Select'
import MenuItem from '@mui/material/MenuItem'
import defaultAvatar from 'assets/mi-banana-icons/default-profile.png'
import OutlinedInput from '@mui/material/OutlinedInput'
import FormControl from '@mui/material/FormControl'
import theme from 'assets/theme'
import CloseSharp from '@mui/icons-material/CloseSharp';
import KeyboardArrowDownIcon from '@mui/icons-material/KeyboardArrowDown';
import { useSelector } from 'react-redux'
import apiClient from 'api/apiClient'

const ITEM_HEIGHT = 48;
const ITEM_PADDING_TOP = 8;
const MenuProps = {
    PaperProps: {
        style: {
            maxHeight: ITEM_HEIGHT * 4.5 + ITEM_PADDING_TOP,
        },
    },
};
const SelectMembers = ({ handleChange, memberName, setMemberName, manager }) => {
    const [designerList, setDesignerList] = useState([])
    // const c = [{}]
    // if (Array.isArray(c) && c.length > 0) {
    //     const firstElment = c[0]
    //     if (typeof firstElment === 'string') {
    //         return <FormControl sx={formcontrolSx}>
    //             <Select
    //                 placeholder='Select members to assign project'
    //                 input={<OutlinedInput name='members' />}
    //                 onChange={handleChange}
    //                 value={memberName}
    //             >
    //                 {member.map((item =>
    //                     <MenuItem
    //                         value={item.id}
    //                         key={item.id}
    //                     // style={getStyles(item.name, personName, theme)}
    //                     // IconComponent={() => personName ? <CloseSharp fontSize='small' sx={{ marginRight: 1, cursor : "pointer" }} onClick={() => setPersonName("") } /> : <KeyboardArrowDownIcon fontSize='medium' sx={{ marginRight: 1, cursor : "pointer" }} />}
    //                     >
    //                         {item.name}
    //                     </MenuItem>
    //                 ))}
    //             </Select>
    //         </FormControl>
    //     }
    // }
    const id = useSelector(state => state.userDetails?.id)

    useEffect(() => {
        if (manager) {
            apiClient.get("/api/get-designer-list/" + id)
                .then(({ data }) => setDesignerList(data?.designerlist))
                .catch(e => console.log('Getting designer list ', e?.response?.data?.message))
        }
    }, [])
    console.log(memberName)
    return (
        <FormControl sx={formcontrolSx}>
            <Select
                placeholder='Select members to assign project'
                input={<OutlinedInput value={memberName} onChange={handleChange} name='members' sx={{ paddingBlock: '18px' }} />}
                MenuProps={MenuProps}
            >
                {designerList?.length ? designerList.map((item =>
                    <MenuItem
                        key={item._id}
                        sx={selectSX}
                        value={item}
                        IconComponent={() => memberName ?
                            <CloseSharp
                                fontSize='small'
                                sx={{ marginRight: 1, cursor: "pointer" }}
                                onClick={() => setMemberName("")} />
                            :
                            <KeyboardArrowDownIcon fontSize='medium'
                                sx={{ marginRight: 1, cursor: "pointer" }} />}
                    >
                        {item.name}
                    </MenuItem>
                )) : null}
            </Select>
        </FormControl>
    )
}

export default SelectMembers


const formcontrolSx = {
    m: 1,
    width: 180,
    height: 60,
    background: 'white',
    marginBottom: 1.7
}

const selectSX = {
    paddingBlock: "14px",
    // border: "2px solid #000",
    "& .MuiInputBase-root": {
        paddingBlock: '12px',
        backgroundColor: 'red',
    },
    "&:hover": {
        borderColor: 'transparent',
    },
    "& fieldset": {
        borderColor: '#000' + ' !important',
        borderWidth: 0 + " !important",
        "&:focus": {
            borderWidth: 0,
            borderColor: `transparent` + " !important",
        }
    },
    "& .MuiMenu-paper": {
        backgroundColor: "red",

    }
}