import React, { useState } from 'react'
import MiLayoutCover from '../components/Mi-Layout'
import MDBox from 'components/MDBox'
import MiIcon from 'assets/mi-banana-icons/mibanana-logo-1-color 1.png'
import { Checkbox, FormControlLabel, Grid, IconButton, TextField } from '@mui/material'
import CoverImage from 'assets/mi-banana-icons/Photo.png';
import MDTypography from 'components/MDTypography'
import MDInput from 'components/MDInput'
import { Link, useNavigate } from 'react-router-dom'
import MDSnackbar from 'components/MDSnackbar'
import MDButton from 'components/MDButton'
import Visibility from '@mui/icons-material/Visibility';
import VisibilityOff from '@mui/icons-material/VisibilityOff';
import apiClient from "api/apiClient";
import { ArrowForward } from '@mui/icons-material';
import { useMediaQuery } from '@mui/material'
import { MoonLoader } from 'react-spinners'

const MiSignup = () => {
    const [name, setName] = useState("")
    const [email, setEmail] = useState("")
    const [password, setPassword] = useState("")
    const [confirm_password, setConfirmPassword] = useState("")
    const [loading, setLoading] = useState(false)
    const [isCheck, setIsCheck] = useState(false)
    const navigate = useNavigate()

    const [respMessage, setRespMessage] = useState("")
    const [errorSB, setErrorSB] = useState(false);
    const [successSB, setSuccessSB] = useState(false);

    const openSuccessSB = () => setSuccessSB(true);
    const closeSuccessSB = () => setSuccessSB(false);

    const openErrorSB = () => setErrorSB(true);
    const closeErrorSB = () => setErrorSB(false);

    const [showPassword, setShowPassword] = React.useState(false);
    const [showConfirmPassword, setShowConfirmPassword] = React.useState(false);

    const handleClickShowPassword = () => setShowPassword((show) => !show);
    const handleClickConfirmPassword = () => setShowConfirmPassword((showConfirmPassword) => !showConfirmPassword);

    const isSmall = useMediaQuery("(min-width:1000px)")

    const handleSignUp = async (event) => {
        event.preventDefault()
        if (confirm_password !== password || !name && !email) return
        else if (!isCheck) return
        const data = {
            name,
            email,
            password,
            roles: ["Customer"]
        }
        setLoading(true)
        await apiClient.post('/authentication/mi-sign-up', data)
            .then(resp => {
                if (resp.status === 200) {
                    setLoading(false)
                    setRespMessage(resp?.data.message)
                    openSuccessSB()
                    setEmail("")
                    setPassword("")
                    setTimeout(() => {
                        openSuccessSB()
                        navigate("/authentication/sign-in")
                    }, 1200)
                }
                setLoading(false)
            })
            .catch(err => {
                if (err?.response) {
                    console.error(err.response.data.message)
                    setRespMessage(err.response.data.message)
                    setLoading(false)
                    setTimeout(() => {
                        openErrorSB()
                    }, 1200)
                }
                console.log(err?.message)
            })
    }

    const renderErrorSB = (
        <MDSnackbar
            color="error"
            icon="warning"
            title="Error"
            content={respMessage}
            dateTime={new Date().toLocaleTimeString('pk')}
            open={errorSB}
            onClose={closeErrorSB}
            close={closeErrorSB}
            bgWhite
        />
    );

    const renderSuccessSB = (
        <MDSnackbar
            color="success"
            icon="check"
            title="SUCCESS"
            content={respMessage}
            dateTime={new Date().toLocaleTimeString('pk')}
            open={successSB}
            onClose={closeSuccessSB}
            close={closeSuccessSB}
            bgWhite
        />
    );

    const Styles = {
        fontWeight: "bold",
        fontSize: "15px",
        marginLeft: 4
    }

    return (
        <MiLayoutCover>
            <MDBox
                bgColor="white"
                sx={({ palette: { light } }) => ({
                    width: "calc(100%)",
                    height: "100vh",
                    display: "flex",
                    justifyContent: "center",
                    alignItems: "center",
                    flexDirection: "column",
                    background: light.cream

                })}
            >
                <MDBox display="flex"
                    justifyContent="center"
                    alignItems="center"
                    mt={-10}
                    pb={4}
                >
                    <img src={MiIcon} width={isSmall ? "100%" : "60%"} />
                </MDBox>
                <Grid container justifyContent={"center"} >
                    <Grid item xxl={3} xl={4} lg={5} sx={{ display: isSmall ? "block" : "none", boxShadow: "4px 3px 7px -2px #cccccc0d" }}>
                        <img src={CoverImage} width={"100%"} height={"100%"} />
                    </Grid>
                    <Grid item xxl={3} xl={4} lg={5} md={6} xs={10} sx={{ background: 'white', boxShadow: "4px 3px 7px -2px #cccccc0d" }}>
                        <MDBox pt={4} pb={3} px={3} >
                            <MDBox component="form" role="form" onSubmit={handleSignUp} >
                                <MDTypography pb={2}
                                    verticalAlign="middle"
                                    fontWeight="medium"
                                    sx={({ palette: { mibanana }, typography: { size } }) => ({
                                        color: mibanana.text,
                                        textAlign: "center",
                                        size: size.lg

                                    })}
                                >Create Account</MDTypography>
                                <Grid container spacing={2}>
                                    <Grid item lg={6} xs={12} md={12}>
                                        <MDBox mb={2}>
                                            <label style={Styles} htmlFor='Name'>Name</label>
                                            <MDInput type="text" value={name} onChange={(e) => setName(e.target.value)} placeholder="Full Name" variant="outlined" fullWidth />
                                            {name ? null : <span style={{ color: "red", fontSize: '12px' }}>Name is required</span>}
                                        </MDBox>
                                    </Grid>
                                    <Grid item lg={6} xs={12} md={12}>
                                        <MDBox mb={2}>
                                            <label style={Styles} htmlFor='Email'>Email</label>
                                            <MDInput type="email" value={email} onChange={(e) => setEmail(e.target.value)} placeholder="Email address" variant="outlined" fullWidth />
                                            {email ? null : <span style={{ color: "red", fontSize: '12px' }}>Email is required</span>}
                                        </MDBox>
                                    </Grid>
                                </Grid>
                                <Grid container spacing={2}>
                                    <Grid item xxl={12} lg={12} xs={12} md={12}>
                                        <MDBox mb={2} sx={{ position: "relative" }}>
                                            <label style={Styles} htmlFor='Password'>Password</label>
                                            <MDInput type={showPassword ? "text" : "password"} value={password} onChange={(e) => setPassword(e.target.value)} placeholder="Password" variant="outlined" fullWidth />
                                            <IconButton
                                                onClick={handleClickShowPassword}
                                                sx={{
                                                    position: "absolute",
                                                    right: 0,
                                                }}
                                            >
                                                {showPassword ? <Visibility /> : <VisibilityOff />}
                                            </IconButton>
                                            {password === confirm_password ? null : <span style={{ color: "red", fontSize: '12px' }}>password and conf password not matched</span>}
                                        </MDBox>
                                    </Grid>
                                </Grid>
                                <Grid container>
                                    <Grid item xl={12} xs={12} lg={12} md={12}>
                                        <MDBox mb={2} sx={{ position: "relative" }}>
                                            <label style={Styles} htmlFor='ConfirmPassword'>Confirm Password</label>
                                            <MDInput type={showConfirmPassword ? "text" : "password"} value={confirm_password} onChange={(e) => setConfirmPassword(e.target.value)} placeholder="Confirm Password" variant="outlined" fullWidth />
                                            <IconButton
                                                onClick={handleClickConfirmPassword}
                                                sx={{
                                                    position: "absolute",
                                                    right: 0,
                                                }}
                                            >
                                                {showConfirmPassword ? <Visibility /> : <VisibilityOff />}
                                            </IconButton>
                                            {password === confirm_password ? null : <span style={{ color: "red", fontSize: '12px' }}>Conf password and password not matched</span>}
                                        </MDBox>
                                    </Grid>
                                </Grid>
                                <MDBox display="flex" alignItems="center" ml={-1}>
                                    <FormControlLabel required control={<Checkbox checked={isCheck}
                                        onChange={() => setIsCheck(prev => !prev)} />} />
                                    <MDTypography
                                        variant="button"
                                        fontWeight="regular"
                                        color="text"
                                        sx={{ cursor: "pointer", userSelect: "none", ml: -1 }}
                                    >
                                        &nbsp;&nbsp;I agree the&nbsp;
                                    </MDTypography>
                                    <MDTypography
                                        component="a"
                                        href="#"
                                        variant="button"
                                        fontWeight="bold"
                                        color="info"
                                        textGradient
                                    >
                                        Terms and Conditions
                                    </MDTypography>
                                </MDBox>
                                <MDBox mt={4} mb={1} pt={3}>
                                    <MDButton type="submit" color="warning" fullWidth
                                        circular={true}
                                        sx={{
                                            color: '#000 !important',
                                            fontSize: 14,
                                            textTransform: "capitalize"
                                        }}
                                    >
                                        Submit &nbsp; <ArrowForward fontSize='large' />&nbsp;
                                        <MoonLoader loading={loading} size={23} color='#121212' />

                                    </MDButton>
                                </MDBox>

                            </MDBox>
                        </MDBox>
                    </Grid>
                </Grid>
                <MDBox mt={3} mb={1} textAlign="center">
                    <MDTypography variant="button" color="text">
                        Already have an account?{" "}
                        <MDTypography
                            component={Link}
                            to="/authentication/mi-sign-in"
                            variant="button"
                            color="info"
                            fontWeight="medium"
                            textGradient
                        >
                            Sign In
                        </MDTypography>
                        {renderErrorSB}
                        {renderSuccessSB}
                    </MDTypography>
                </MDBox>

            </MDBox>
        </MiLayoutCover>
    )
}

export default MiSignup
