import Card from '@mui/material/Card'
import Grid from '@mui/material/Grid'
import MDBox from 'components/MDBox'
import MDTypography from 'components/MDTypography'
import DashboardLayout from 'examples/LayoutContainers/DashboardLayout'
import DashboardNavbar from 'examples/Navbars/DashboardNavbar'
import ProjectDataTable from 'examples/projectsTable'
import React, { useEffect, useState } from 'react'
import authorsTableData from 'layouts/ProjectsTable/data/authorsTableData'
import ProjectStatus from 'examples/Statuses'
import reduxContainer from 'redux/containers/containers'
import { Author, Action, Job } from './data/authorsTableData'
import MDBadge from 'components/MDBadge'
import { getProjectData } from 'redux/global/global-functions'
import { useSelector } from 'react-redux'
import { Link, useNavigate } from 'react-router-dom'
import MDSnackbar from 'components/MDSnackbar'
import CreateProject from 'examples/Navbars/Form-modal'
import { setAlert } from 'redux/actions/actions'
import { useDispatch } from 'react-redux'


const ProjectTable = ({ reduxState, reduxActions }) => {
    const { columns } = authorsTableData()
    const navigate = useNavigate()
    const { project_list, userDetails } = reduxState
    const [respMessage, setRespMessage] = useState("")
    const [open, setOpen] = useState(false);


    const [errorSB, setErrorSB] = useState(false);
    const [successSB, setSuccessSB] = useState(false);
    const isModalShow = useSelector(state => state.isModalShow)
    const projectList = useSelector(state => state.project_list.CustomerProjects)
    const user = useSelector(state => state.userDetails)

    const openSuccessSB = () => setSuccessSB(true);
    const closeSuccessSB = () => setSuccessSB(false);
    const openErrorSB = () => setErrorSB(true);
    const closeErrorSB = () => setErrorSB(false);
    const dispatch = useDispatch()

    const [formValue, setFormValue] = useState({
        project_category: '',
        design_type: '',
        brand: '',
        project_title: '',
        project_description: '',
        describe_audience: '',
        sizes: '',
        resources: '',
        reference_example: '',
        add_files: [],
        specific_software_names: '',
    })

    const handleChange = (e) => {
        e.stopPropagation();
        const { name, value } = e.target
        setFormValue({
            ...formValue,
            [name]: value
        })
    }

    const handleOpen = () => {
        dispatch(setAlert(true))
    }

    function projectActiveorNot(id) {
        // console.log('ID =>', id)
        reduxActions.getID(id)
        let projectID = projectList[id].hasOwnProperty("_id") ? projectList[id]?._id : projectList[id]?.id
        if (user?.roles?.includes("Project-Manager") || user?.roles?.includes("Graphic-Designer") || user?.roles?.includes("Admin")) {
            navigate("/chat/" + projectID)
            return
        } else {
            projectList[id]?.is_active ? navigate("/chat/" + projectID) : handleOpen()
        }
    }

    const handleClose = () => {
        reduxActions.showModal(false)
    };

    function projectStatus(status) {
        switch (status) {
            case 'Approval':
                return "Approval"
            case 'Completed':
                return "Completed"
            case 'Ongoing':
                return "Ongoing"
            case 'HeadsUp':
                return "HeadsUp"
            default:
                return "End"
        }
    }
    function getStatusStyle(status) {
        switch (status) {
            case 'Approval':
                return "#c5495d"
            case 'Completed':
                return "#FFE135"
            case 'Ongoing':
                return "#FFE135"
            case 'HeadsUp':
                return "#FFE135"
            default:
                return "#c5495d"
        }
    }
    const rows = project_list?.CustomerProjects?.length ? project_list.CustomerProjects.map((item, i) => {
        const date = new Date(item.createdAt);
        const year = date.getFullYear();
        const month = String(date.getMonth() + 1).padStart(2, "0"); // Adding 1 because months are zero-indexed
        const day = String(date.getDate()).padStart(2, "0");
        let hours = date.getHours();
        const minutes = String(date.getMinutes()).padStart(2, "0");
        const seconds = String(date.getSeconds()).padStart(2, "0");
        let ampm = "AM";

        // Convert to 12-hour format and set AM/PM
        if (hours >= 12) {
            ampm = "PM";
            if (hours > 12) {
                hours -= 12;
            }
        }
        hours = String(hours).padStart(2, "0");
        const readableTimestamp = `${year}-${month}-${day} ${hours}:${minutes}:${seconds} ${ampm}`;

        console.log('=>', item)
        // onClick={() => isfunc === true ? projectActiveorNot(row?.id) : null}
        return {
            name: (
                <MDBox lineHeight={1}>
                    <MDTypography display="block" sx={{ textDecoration: 'underline ' + '!important' }} variant="button" fontWeight="medium">
                        <MDBox sx={{ "&:hover": { color: "blue" } }} onClick={() => projectActiveorNot(project_list?.CustomerProjects.indexOf(item))}>
                            {item?.name}
                        </MDBox>
                    </MDTypography>
                </MDBox>),

            team_members: <MDTypography display="flex" flexDirection="column" gap="10px">
                {item.team_members?.length > 0 ? item.team_members.map(item => <MDTypography color="#333" variant="h6">{item.name}</MDTypography>) :
                    <MDTypography color="#333" fontSize="small" variant="h6">Currently not Assigned to <br /> any Team members</MDTypography>}
            </MDTypography>,

            status: <MDBox ml={-1}>
                <MDBadge badgeContent={projectStatus(item?.status)}
                    sx={{
                        "& .MuiBadge-badge":
                            { background: getStatusStyle(item?.status), color: 'dark', textTransform: 'capitalize', fontSize: ".8rem" }
                    }} circular="true" size="lg" />
            </MDBox>,

            project_category: <MDTypography variant="h6" color="text" fontWeight="medium">
                {item.project_category}
            </MDTypography>,
            active: <MDTypography color="dark" variant="p">{!item.is_active ? "Not Active" : item.updatedAt}</MDTypography>,
            createdAt: <MDTypography color="dark" variant="p">{readableTimestamp}</MDTypography>,
            action: <MDTypography component="a" href="#" variant="caption" color="text" fontWeight="medium">
                <Action />
            </MDTypography>

        }
    }) : []

    useEffect(() => {
        const id = reduxState?.userDetails?.id
        const roles = reduxState?.userDetails?.roles
        getProjectData(id, reduxActions.getCustomerProject)
    }, [])

    const renderErrorSB = (
        <MDSnackbar
            color="error"
            icon="warning"
            title="Error"
            content={respMessage}
            dateTime={new Date().toLocaleTimeString('pk')}
            open={errorSB}
            onClose={closeErrorSB}
            close={closeErrorSB}
            bgWhite
        />
    );
    const renderSuccessSB = (
        <MDSnackbar
            color="success"
            icon="check"
            title="SUCCESS"
            content={respMessage}
            dateTime={new Date().toLocaleTimeString('pk')}
            open={successSB}
            onClose={closeSuccessSB}
            close={closeSuccessSB}
            bgWhite
        />
    );
    let statuses = [
        "All",
        "Archived",
        "Cancelled",
        "Approval",
        "Completed",
        "Ongoing",
        "For Review",
        "Draft",
        "Heads Up!",
    ]
    return (
        <DashboardLayout>
            <DashboardNavbar />
            {/* <CreateProject
                formValue={formValue}
                setFormValue={setFormValue}
                open={isModalShow}
                handleClose={handleClose}
                handleChange={handleChange}
                openSuccessSB={openSuccessSB}
                openErrorSB={openErrorSB}
                setRespMessage={setRespMessage}
            /> */}
            <MDBox pt={6} pb={3}>
                <Grid container spacing={6}>
                    <Grid item xs={12}>
                        <Grid container justifyContent={"space-between"} alignItems={"center"} width={"40%"}>
                            <Grid item xl={4} lg={4} md={3}>
                                <ProjectStatus data={statuses} status={"STATUS"} />
                            </Grid>
                            <Grid item xl={4} lg={4} md={4}>
                                <ProjectStatus data={reduxState.category} status={"CATEGORY"} />
                            </Grid>
                            <Grid item xl={4} lg={4} md={4}>
                                <ProjectStatus data={reduxState.brand} status={"BRAND"} />
                            </Grid>
                        </Grid>
                        <Card>
                            <MDBox
                                mx={2}
                                mt={3}
                                sx={({ palette: { light, grey } }) => ({
                                    // backgroundColor : light.cream,
                                })}

                            // py={3}
                            // px={2}
                            // variant="gradient"   
                            // bgColor="info"
                            // coloredShadow="color.mibanana"
                            // borderRadius="lg"
                            // coloredShadow="info"
                            >
                                <MDTypography variant="h6" color="dark">
                                    MiProjects
                                </MDTypography>
                            </MDBox>
                            <MDBox pt={3}>
                                <ProjectDataTable
                                    table={{ columns, rows }}
                                    isSorted={false}
                                    entriesPerPage={false}
                                    showTotalEntries={false}
                                    isfunc={true}
                                    noEndBorder
                                />
                                {!rows.length ? <MDTypography textAlign="center" p={1} component="h4">No Projects Found</MDTypography> : null}
                            </MDBox>
                        </Card>
                    </Grid>
                </Grid>
                {renderSuccessSB}
                {renderErrorSB}
            </MDBox>
        </DashboardLayout>
    )
}

export default reduxContainer(ProjectTable)
