export const Author = ({ name, }) => (
  <MDBox lineHeight={1}>
    <MDTypography display="block" variant="button" fontWeight="medium">
      {name}
    </MDTypography>
  </MDBox>
);

export const Job = ({ title, description }) => (
  <MDBox lineHeight={1} textAlign="left">
    <MDTypography display="block" variant="caption" color="text" fontWeight="medium">
      {title}
    </MDTypography>
    <MDTypography variant="caption">{description}</MDTypography>
  </MDBox>
);

export const Action = ({ children }) => {
  const [anchorEl, setAnchorEl] = useState(null)
  const handleMenuOpen = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleMenuClose = () => {
    setAnchorEl(null);
  };

  return (
    <MDBox>
      <MDBox bgColor={anchorEl ? "dark" : "warning"}
        sx={{
          width: "57px",
          borderRadius: "50px"
        }}
      >

        <MoreVertIcon onClick={handleMenuOpen} sx={{ height: "2em", fontSize: '27px !important', fill: anchorEl ? "white" : "black" }} />
      </MDBox>
      {children ? children :
        <Menu
          id="dropdown-menu"
          anchorEl={anchorEl}
          open={Boolean(anchorEl)}
          onClose={handleMenuClose}
          transformOrigin={{
            vertical: "top",
            horizontal: "center"
          }}
        >
          <MenuItem onClick={() => { }}>Cancel</MenuItem>
          <MenuItem onClick={() => { }}>Duplicate</MenuItem>
          <MenuItem onClick={() => { }}>Completed</MenuItem>

        </Menu>
      }
    </MDBox >
  )
}
