import React, { useEffect, useState } from 'react'
import MiLayoutCover from '../components/Mi-Layout'
import MDBox from 'components/MDBox'
import MiIcon from 'assets/mi-banana-icons/mibanana-logo-1-color 1.png'
import { Button, Checkbox, Grid, IconButton, TextField } from '@mui/material'
import CoverImage from 'assets/mi-banana-icons/Photo.png';
import MDTypography from 'components/MDTypography'
import MDInput from 'components/MDInput'
import { Link, useNavigate } from 'react-router-dom'
import MDSnackbar from 'components/MDSnackbar'
import MDButton from 'components/MDButton'
import Visibility from '@mui/icons-material/Visibility';
import VisibilityOff from '@mui/icons-material/VisibilityOff';
import apiClient from "api/apiClient";
import { ArrowForward } from '@mui/icons-material';
import { useMediaQuery } from '@mui/material'
import reduxContainer from 'redux/containers/containers'
import { getProjectData } from 'redux/global/global-functions'
import MoonLoader from 'react-spinners/MoonLoader'

const MiSignIn = ({ reduxActions, reduxState }) => {
    const [email, setEmail] = useState("")
    const [password, setPassword] = useState("")
    const [loading, setLoading] = useState(false)
    const navigate = useNavigate()

    const [respMessage, setRespMessage] = useState("")
    const [errorSB, setErrorSB] = useState(false);
    const [successSB, setSuccessSB] = useState(false);

    const openSuccessSB = () => setSuccessSB(true);
    const closeSuccessSB = () => setSuccessSB(false);

    const openErrorSB = () => setErrorSB(true);
    const closeErrorSB = () => setErrorSB(false);

    const [showPassword, setShowPassword] = React.useState(false);
    const handleClickShowPassword = () => setShowPassword((show) => !show);

    const isSmall = useMediaQuery("(min-width:1000px)")

    const handleSignIn = async (event) => {
        event.preventDefault();
        const data = {
            email,
            password
        }
        setLoading(true)
        await apiClient.post('/authentication/mi-sign-in', data)
            .then((resp) => {
                if (resp.status === 200) {
                    setEmail("")
                    setPassword("")
                    setRespMessage(resp?.data.message)
                    localStorage.setItem('user_details', JSON.stringify(resp?.data?.userDetails))
                    reduxActions.getUserDetails(resp?.data?.userDetails)
                    const id = resp?.data?.userDetails.id
                    getProjectData(id, reduxActions.getCustomerProject)
                    openSuccessSB()
                    setLoading(false)
                    navigate("/")
                } 
            })
            .catch(err => {
                if (err?.response) {
                    console.error(err.response.data.message)
                    setRespMessage(err.response.data.message)
                    setLoading(false)
                    setTimeout(() => {
                        openErrorSB()
                    }, 1200)
                }
                console.log(err?.message)
            })
    }

    const renderErrorSB = (
        <MDSnackbar
            color="error"
            icon="warning"
            title="Error"
            content={respMessage}
            dateTime={new Date().toLocaleTimeString('pk')}
            open={errorSB}
            onClose={closeErrorSB}
            close={closeErrorSB}
            bgWhite
        />
    );

    const renderSuccessSB = (
        <MDSnackbar
            color="success"
            icon="check"
            title="SUCCESS"
            content={respMessage}
            dateTime={new Date().toLocaleTimeString('pk')}
            open={successSB}
            onClose={closeSuccessSB}
            close={closeSuccessSB}
            bgWhite
        />
    );

    const Styles = {
        fontWeight: "bold",
        fontSize: "15px",
        marginLeft: 4
    }

    useEffect(() => {
        
        return () => {
            setEmail("")
            setPassword("")
            setLoading(false)
            setRespMessage("")
        }
    }, [])
    return (
        <MiLayoutCover>
            <MDBox
                bgColor="white"
                sx={({ palette: { light } }) => ({
                    width: "calc(100%)",
                    height: "100vh",
                    display: "flex",
                    justifyContent: "center",
                    alignItems: "center",
                    flexDirection: "column",
                    background: light.cream

                })}
            >
                <MDBox display="flex"
                    justifyContent="center"
                    alignItems="center"
                    mt={-10}
                    pb={4}
                >
                    <img src={MiIcon} width={isSmall ? "100%" : "60%"} />
                </MDBox>
                <Grid container justifyContent={"center"} >
                    <Grid item xxl={3} xl={4} lg={5} sx={{ display: isSmall ? "block" : "none", boxShadow: "4px 3px 7px -2px #cccccc0d" }}>
                        <img src={CoverImage} width={"100%"} height={"100%"} />
                    </Grid>
                    <Grid item xxl={3} xl={4} lg={5} md={6} xs={10} sx={{ background: 'white', boxShadow: "4px 3px 7px -2px #cccccc0d" }}>
                        <MDBox pt={4} pb={3} px={3} >
                            <MDBox component="form" role="form" onSubmit={handleSignIn} >
                                <MDTypography py={3}
                                    verticalAlign="middle"
                                    fontWeight="medium"
                                    sx={({ palette: { mibanana }, typography: { size } }) => ({
                                        color: mibanana.text,
                                        textAlign: "center",
                                        fontSize: '1.5rem',

                                    })}
                                >Login to your account</MDTypography>
                                <Grid container spacing={2} >
                                    <Grid item lg={12} xs={12} md={12}>
                                        <MDBox mb={2}>
                                            <label style={Styles} htmlFor='Email'>Email</label>
                                            <MDInput type="email" required value={email} onChange={(e) => setEmail(e.target.value)} placeholder="Email address" variant="outlined" fullWidth />
                                        </MDBox>
                                    </Grid>
                                </Grid>
                                <Grid container spacing={2}>
                                    <Grid item xxl={12} lg={12} xs={12} md={12}>
                                        <MDBox mb={2} sx={{ position: "relative" }}>
                                            <label style={Styles} htmlFor='Password'>Password</label>
                                            <MDInput type={showPassword ? "text" : "password"} value={password} onChange={(e) => setPassword(e.target.value)} placeholder="Password" variant="outlined" required fullWidth />
                                            <IconButton
                                                onClick={handleClickShowPassword}
                                                sx={{
                                                    position: "absolute",
                                                    right: 0,
                                                }}
                                            >
                                                {showPassword ? <Visibility /> : <VisibilityOff />}
                                            </IconButton>
                                        </MDBox>
                                    </Grid>
                                </Grid>
                                {/* <MDTypography
                                    component={Link}
                                    to="/settings/forget-password"
                                    variant="button"
                                    color="warning"
                                    fontWeight="medium"
                                    textGradient
                                    sx={{
                                        textAlign: "right",
                                        display: "block"
                                    }}
                                >
                                    forget password
                                </MDTypography> */}
                                <MDBox mt={4} mb={1} pt={3}>
                                    <MDButton
                                        type="submit"
                                        color="warning"
                                        fullWidth
                                        circular={true}
                                        sx={{
                                            color: '#000 !important',
                                            fontSize: 14,
                                            textTransform: "capitalize"
                                        }}
                                    >
                                        Login &nbsp; <ArrowForward fontSize='large' />&nbsp;
                                        <MoonLoader loading={loading} size={23} color='#121212' />
                                    </MDButton>
                                </MDBox>

                            </MDBox>
                        </MDBox>
                    </Grid>
                </Grid>
                <MDBox mt={3} mb={1} textAlign="center">
                    <MDTypography variant="button" color="text">
                        Don't have an account?{" "}
                        <MDTypography
                            component={Link}
                            to="/authentication/mi-sign-up"
                            variant="button"
                            color="info"
                            fontWeight="medium"
                            textGradient
                        >
                            Create Account
                        </MDTypography>
                        {renderErrorSB}
                        {renderSuccessSB}
                    </MDTypography>
                </MDBox>

            </MDBox>
        </MiLayoutCover>
    )
}

export default reduxContainer(MiSignIn)
