import React, { useEffect, useState } from 'react'
import OutlinedInput from '@mui/material/OutlinedInput';
import InputLabel from '@mui/material/InputLabel';
import MenuItem from '@mui/material/MenuItem';
import FormControl from '@mui/material/FormControl';
import Select from '@mui/material/Select';
import MDBox from 'components/MDBox';
import { useTheme } from '@mui/material';
import KeyboardArrowDownIcon from '@mui/icons-material/KeyboardArrowDown';
import { CloseSharp } from '@mui/icons-material';
import reduxContainer from 'redux/containers/containers';

const ITEM_HEIGHT = 48;
const ITEM_PADDING_TOP = 8;
const MenuProps = {
    PaperProps: {
        style: {
            maxHeight: ITEM_HEIGHT * 4.5 + ITEM_PADDING_TOP,
        },
    },
};

const names = [
    'Oliver Hansen',
    'Van Henry',
    'April Tucker',
    'Ralph Hubbard',
    'Omar Alexander',
    'Carlos Abbott',
    'Miriam Wagner',
    'Bradley Wilkerson',
    'Virginia Andrews',
    'Kelly Snyder',
];

function getStyles(name, personName, theme) {
    return {
        fontWeight:
            personName.indexOf(name) === -1
                ? theme.typography.fontWeightRegular
                : theme.typography.fontWeightMedium,
    };
}


const ProjectStatus = ({ data, status, reduxState, reduxActions }) => {
    const [personName, setPersonName] = React.useState('');
    const [copyProjectList,setCopyProjectList] = useState({})
    const theme = useTheme()

    const handleChange = (event) => {
        const {
            target: { value },
        } = event;
        setCopyProjectList(reduxState.project_list)
        if (value) {
            const filterAccordingtoStatus = reduxState?.project_list?.CustomerProjects?.filter(item => {
                return item.status === value
            })
            console.log('FILTER => ', filterAccordingtoStatus)
            reduxActions.getCustomerProject({ CustomerProjects: filterAccordingtoStatus })
        }
        // setPersonName(
        //     // On autofill we get a stringified value.
        //     typeof value === 'string' ? value.split(',') : value,
        // );
        setPersonName(value)
    };

    const clearValue = () => {
        setPersonName("")
        // console.log(copyProjectList)
        reduxActions.getCustomerProject(copyProjectList)
    }
    // useEffect(() => {
    // }, [])

    return (
        <MDBox>
            <FormControl sx={{ m: 1, width: 180, height: 60, background: 'white', marginBottom: 1.7 }}>
                <InputLabel id="demo-multiple-name-label" disableAnimation={true} size="small" classes={`.Mui-focused`} >{status}</InputLabel>
                <Select
                    labelId="demo-multiple-name-label"
                    autoWidth
                    id="demo-multiple-name"
                    value={personName}
                    onChange={handleChange}
                    // input={<OutlinedInput label="Name" />}
                    MenuProps={MenuProps}
                    IconComponent={() => personName ? <CloseSharp fontSize='small' sx={{ marginRight: 1, cursor: "pointer" }} onClick={clearValue} /> : <KeyboardArrowDownIcon fontSize='medium' sx={{ marginRight: 1, cursor: "pointer" }} />}

                    sx={{
                        paddingBlock: "14px",
                        border: "2px solid #000",
                        "&:hover": {
                            borderColor: 'transparent',
                        },
                        "& fieldset": {
                            borderColor: '#000' + ' !important',
                            borderWidth: 0 + " !important",
                            "&:focus": {
                                borderWidth: 0,
                                borderColor: `transparent` + " !important",
                            }
                        },
                        "& .MuiMenu-paper": {
                            backgroundColor: "red !important",

                        }
                    }}
                >
                    {data?.map((name) => (
                        <MenuItem
                            key={name}
                            value={name}
                            style={getStyles(name, personName, theme)}
                        >
                            {name}
                        </MenuItem>
                    ))}
                </Select>
            </FormControl>
        </MDBox>
    )
}

export default reduxContainer(ProjectStatus)
