import Card from '@mui/material/Card'
import MDBox from 'components/MDBox'
import React, { useState } from 'react'
import "./brand-table.css"
import DashboardNavbar from 'examples/Navbars/DashboardNavbar'
import DashboardLayout from 'examples/LayoutContainers/DashboardLayout'
import Button from '@mui/material/Button'
import Grid from '@mui/material/Grid'
import Add from '@mui/icons-material/Add'
import BrandForm from './Add-brand-form'
import apiClient from 'api/apiClient'
import MDSnackbar from 'components/MDSnackbar'
import ProjectDataTable from 'examples/projectsTable'
import useBrandData from './brandData'
import MDTypography from 'components/MDTypography'
import { useDispatch } from 'react-redux'
import { getNew_Brand } from 'redux/actions/actions'
import { useSelector } from 'react-redux'
import { getBrandData } from 'redux/global/global-functions'
import reduxContainer from 'redux/containers/containers'

const MIBrandTable = ({ reduxState, reduxActions }) => {
    const [open, setOpen] = useState(false)
    const [loading, setLoading] = useState(false)
    const [other, setOther] = useState(1)
    const [addMoreField, setAddMore] = useState([])
    const id = useSelector(state => state.userDetails.id)
    const [errorSB, setErrorSB] = useState(false);
    const [successSB, setSuccessSB] = useState(false);
    const [respMessage, setRespMessage] = useState("")
    const [image, setImage] = useState({
        upload_logo: [],
        upload_moodboard: [],
        replace_brand_guidelines: [],
        upload_more: []
    })

    const { rows, columns } = useBrandData()
    const [formValue, setFormValue] = useState({
        brand_name: '', brand_description: '', web_url: '',
        facebook_url: '', instagram_url: '', twitter_url: '',
        linkedin_url: '', tiktok_url: ''
    })
    const dispatch = useDispatch()
    const new_brand = useSelector(state=>state.new_brand)
    const userID = useSelector(state => state.userDetails.id)
    // const rows = [
    //     {brand_name : "Nike",brand_description:'asdafdsfasdf  sdfasdf  fa sdf as',action : "Action",logo:"Image",files:"Not Found"}
    // ]

    const openSuccessSB = () => setSuccessSB(true);
    const closeSuccessSB = () => setSuccessSB(false);

    const openErrorSB = () => setErrorSB(true);
    const closeErrorSB = () => setErrorSB(false);


    const handleOpen = () => setOpen(true)
    const handleClose = () => {
        setOpen(false)
        setOther(1)
        setAddMore([])
    }

    const onhandleChange = (event) => {
        event.stopPropagation();
        const { name, value } = event.target
        setFormValue({
            ...formValue,
            [name]: value,
            user: id
        })
    }
    const handleFileUpload = (event) => {
        const { name, files } = event.target
        setImage(prev => ({ ...prev, [name]: files }))
    }
    const addMore = () => {
        setOther(other + 1)
        setAddMore(prev => [...prev, {
            name: `others_${other}`,
            value: ''

        }])
        setImage({
            ...image,
            [`others_${other}`]: []
        })
    }
    const onSubmit = async () => {
        setLoading(true)
        await apiClient.post("/api/brand", formValue)
            .then(({ data }) => {
                console.log(data)
                setRespMessage(data?.message)
                dispatch(getNew_Brand(true))
                setLoading(false)
                openSuccessSB()
                setTimeout( () => {
                    getBrandData(userID,reduxActions.getCustomerBrand)
                },700)


            })
            .catch((e) => {
                if (e?.response) {
                    dispatch(getNew_Brand(!new_brand))
                    setRespMessage(e.response?.data?.message)
                    setLoading(false)
                    openErrorSB()
                    console.log(e?.response)
                }
                setLoading(false)
            })
    }
    // const columns = [
    //     { Header: "Project Name", accessor: "project_name", align: "left" },
    //     { Header: "Category", accessor: "category", align: "left" },
    //     { Header: "Active on", accessor: "active_on", align: "left" },
    // ]
    // const rows = [
    //     {
    //         id: 1,
    //         author: "Social Media Marketing",
    //         category: "Graphic Designs",
    //         status: `${new Date().toDateString()}`
    //     },
    //     {
    //         id: 2,
    //         author: "Blog",
    //         category: "Copy Writing",
    //         status: `${new Date().toDateString()}`
    //     },
    //     {
    //         id: 3,
    //         author: "Instagram Reel",
    //         category: "Video Editing",
    //         status: `${new Date().toDateString()}`
    //     },
    //     {
    //         id: 4,
    //         author: "Instagram Reel",
    //         category: "Video Editing",
    //         status: `${new Date().toDateString()}`
    //     }
    // ]
    const renderErrorSB = (
        <MDSnackbar
            color="error"
            icon="warning"
            title="Error"
            content={respMessage}
            dateTime={new Date().toLocaleTimeString('pk')}
            open={errorSB}
            onClose={closeErrorSB}
            close={closeErrorSB}
            bgWhite
        />
    );

    const renderSuccessSB = (
        <MDSnackbar
            color="success"
            icon="check"
            title="SUCCESS"
            content={respMessage}
            dateTime={new Date().toLocaleTimeString('pk')}
            open={successSB}
            onClose={closeSuccessSB}
            close={closeSuccessSB}
            bgWhite
        />
    );
    const brandModelProps = {
        handleFileUpload, open,
        image, other, addMoreField,
        addMore, loading, onSubmit
    }
    return (
        <DashboardLayout>
            <DashboardNavbar />
            <BrandForm
                state={formValue}
                onChange={onhandleChange}
                onClose={handleClose}
                {...brandModelProps}
            />
            <MDBox ml={4} pt={6} pb={3}>
                <Grid container justifyContent={"flex-end"} alignItems={"center"} spacing={2}>
                    <Grid item xxl={12} xl={12} lg={12} md={12} xs={12} alignSelf={"flex-end"}>
                        <MDBox width={"100%"} sx={{ textAlign: "right" }}>
                            <Button variant="contained" disableFocusRipple type="button" onClick={handleOpen} sx={{ backgroundColor: "#adff2f", borderRadius: 30, "&:hover": { background: "#98e225" }, "&:focus ": { background: "#adff2f " + "!important" } }} disableElevation >
                                <Add cursor={"pointer"} fontSize='large' sx={{ fontSize: '1.3rem !important' }} />
                                &nbsp;{" "}
                                Add Brand
                            </Button>
                        </MDBox>
                    </Grid>
                    <Grid item xxl={12}>
                        <Card sx={{ width: "100%" }}>
                            <ProjectDataTable
                                table={{ rows, columns }}
                                isSorted={false}
                                entriesPerPage={false}
                                showTotalEntries={false}
                                isfunc={false}
                                noEndBorder
                            />
                            {!rows.length ? <MDTypography textAlign="center" p={1} component="h4">No Brands Found</MDTypography> : null}
                        </Card>
                    </Grid>
                </Grid>
            </MDBox>
            {renderErrorSB}
            {renderSuccessSB}
        </DashboardLayout>


    )
}

export default reduxContainer(MIBrandTable)