import React from 'react'
import Dialog from '@mui/material/Dialog'
import DialogActions from '@mui/material/DialogActions'
import DialogTitle from '@mui/material/DialogTitle'
import DialogContent from '@mui/material/DialogContent'
import styled from '@mui/material/styles/styled'
import MDTypography from 'components/MDTypography'
import MDButton from 'components/MDButton'
import CloseOutlined from '@mui/icons-material/CloseOutlined'
import { showFilesModal } from 'redux/global/global-functions'

const ModalLayout = styled(Dialog)(({ theme }) => ({
    "& .MuiDialog-paper": { width: '600px' }
}))

const Modal = ({ children, title, open, onClose, setOpen }) => {

    return (
        <ModalLayout open={open} >
            <DialogTitle
                display={"flex"}
                position={"relative"}
                width={'100%'}
                justifyContent={"space-between"}
                alignItems={"center"}
            >
                <MDTypography color="dark" fontWeight="bold">{title}</MDTypography>
                <MDButton
                    onClick={onClose}
                    sx={{ position: "absolute", right: 4, padding: '1.4rem !important' }}
                >
                    <CloseOutlined
                        sx={{ fill: '#444' }}
                        onClick={() => showFilesModal(open, setOpen)}
                    />
                </MDButton>
            </DialogTitle>
            <DialogContent>
                {children}
                <DialogActions></DialogActions>
            </DialogContent>
        </ModalLayout>
    )
}

export default Modal
